'use client'

import * as React from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"

// Transaction type
export interface Transaction {
  id: number
  merchant_name: string
  merchant_address: string
  txn_date: string
  txn_time: string
  mid: number
  tid: number
  batch_no: number
  stan: number
  txn_type: string
  pan_number: string
  exp_date: string
  invoice_number: number
  card_type: string
  entry_mode: string
  approval_code: number
  rrn: string
  aid: string
  application_name: string
  tvr: string
  tsi: string
  amount: number
  email: string
  mobile: number
  created_at: string
}

export default function Dashboard() {
  const [tid, setTid] = React.useState<string>("")
  const [transactions, setTransactions] = React.useState<Transaction[]>([])
  const [filteredTransactions, setFilteredTransactions] = React.useState<Transaction[]>([])

  // Pagination
  const [currentPage, setCurrentPage] = React.useState<number>(1)
  const itemsPerPage = 10

  // Fetch transactions from API
  React.useEffect(() => {
    fetch("http://127.0.0.1:8000/transactions")
      .then(res => res.json())
      .then((data: Transaction[]) => {
        if (Array.isArray(data)) {
          setTransactions(data)
          setFilteredTransactions(data)
        } else {
          console.error("API did not return an array")
          setTransactions([])
          setFilteredTransactions([])
        }
      })
      .catch(err => console.error("Error fetching transactions:", err))
  }, [])

  // Handle search by TID
  const handleSearch = () => {
    if (!tid) {
      setFilteredTransactions(transactions)
      setCurrentPage(1)
      return
    }
    const filtered = transactions.filter((t) => t.tid.toString().includes(tid))
    setFilteredTransactions(filtered)
    setCurrentPage(1)
  }

  // Pagination logic
  const totalPages = Math.ceil(filteredTransactions.length / itemsPerPage)
  const paginatedTransactions = Array.isArray(filteredTransactions)
    ? filteredTransactions.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage)
    : []

  const handlePrev = () => {
    if (currentPage > 1) setCurrentPage(currentPage - 1)
  }

  const handleNext = () => {
    if (currentPage < totalPages) setCurrentPage(currentPage + 1)
  }
  console.log(transactions[0])
  return (
    <div className="space-y-6">
      {/* Search Card */}
      <Card className="p-4">
        <div className="flex flex-col md:flex-row gap-3 items-start md:items-center w-1/3">
          <Input
            placeholder="Enter TID..."
            value={tid}
            onChange={(e) => setTid(e.target.value)}
            className="flex-1"
          />
          <p>{transactions.length}</p>
          <Button onClick={handleSearch}>Search</Button>
        </div>
      </Card>

      {/* Transaction Table */}
      <Card className="p-4 overflow-x-auto">
        <table className="w-full text-sm table-auto">
          <thead>
            <tr className="bg-muted">
              {[
                "Merchant Name",
                "Merchant Address",
                "Date",
                "Time",
                "MID",
                "TID",
                "Batch No",
                "STAN",
                "Card Number",
                "Card Type",
                "Entry Mode",
                "Invoice No",
                "Amount",
              ].map((col) => (
                <th key={col} className="px-2 py-1 text-left">{col}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {paginatedTransactions.map((t) => (
              <tr key={t.id} className="hover:bg-muted/50 border-b transition-colors">
                <td className="px-2 py-1">{t.merchant_name}</td>
                <td className="px-2 py-1">{t.merchant_address}</td>
                <td className="px-2 py-1">{t.txn_date}</td>
                <td className="px-2 py-1">{t.txn_time}</td>
                <td className="px-2 py-1">{t.mid}</td>
                <td className="px-2 py-1">{t.tid}</td>
                <td className="px-2 py-1">{t.batch_no}</td>
                <td className="px-2 py-1">{t.stan}</td>
                <td className="px-2 py-1">{t.pan_number}</td>
                <td className="px-2 py-1">{t.card_type}</td>
                <td className="px-2 py-1">{t.entry_mode}</td>
                <td className="px-2 py-1">{t.invoice_number}</td>
                <td className="px-2 py-1">{t.amount}</td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* Pagination Controls */}
        <div className="flex justify-end items-center mt-4 gap-4">
          <span>
            Page {currentPage} of {totalPages}
          </span>
          <Button onClick={handlePrev} disabled={currentPage === 1}>Previous</Button>
          <Button onClick={handleNext} disabled={currentPage === totalPages}>Next</Button>
        </div>
      </Card>
    </div>
  )
}
